@media only screen and (max-width: 810px) { .found-bike__left, .found-bike__right { width: 100%; padding: 10px 0; } }
.found-bike__title { font-family: "lato"; font-size: 28px; color: #fff; }
.found-bike__orange { font-weight: 800; color: #ed4d21; }
.found-bike__descr { font-size: 17px; color: #666666; }

.footer { background-color: #141414; color: #929292; }
.footer__row { display: flex; flex-direction: row; }
@media only screen and (max-width: 1024px) { .footer__row { padding: 0 20px; } }
@media only screen and (max-width: 850px) { .footer__row { flex-wrap: wrap; } }
@media only screen and (max-width: 500px) { .footer__row { padding: 0 50px; } }
.footer__col { width: 25%; }
@media only screen and (max-width: 850px) { .footer__col { width: 50%; } }
@media only screen and (max-width: 500px) { .footer__col { width: 100%; } }

.contacts { background: url("../img/bg-contacts.jpg") no-repeat top right scroll; padding-top: 10px; }
@media only screen and (max-width: 900px) { .contacts { background: #141414; } }
.contacts__in { text-align: center; max-width: 800px; background: rgba(20, 20, 20, 0.97); color: #999; font-size: 17px; line-height: 1.9; margin: 81px auto 69px auto; padding: 80px 30px; }
@media only screen and (max-width: 900px) { .contacts__in { max-width: 100%; } }
.contacts h5 { color: #fff; font-size: 21px; font-weight: bold; text-transform: uppercase; margin-bottom: 15px; }

.news { background-color: #F7F7F7; font-family: "lato"; padding-top: 10px; }
.news__left { width: 20%; margin: 0 10px; }
@media only screen and (max-width: 850px) { .news__left { width: 30%; } }
@media only screen and (max-width: 650px) { .news__left { width: 20%; } }
.news__right { width: 80%; }
@media only screen and (max-width: 850px) { .news__right { width: 70%; } }
@media only screen and (max-width: 650px) { .news__right { width: 80%; } }
.news__center { padding-bottom: 12px; }
.news__list { display: flex; flex-direction: row; padding: 50px 0 168px; }
@media only screen and (max-width: 650px) { .news__list { flex-wrap: wrap; } }
.news__el { display: flex; }
@media only screen and (max-width: 650px) { .news__el { width: 100%; padding: 20px; } }
.news__date { width: 60px; height: 60px; background-color: #ed5126; color: #fff; text-align: center; font-family: "Roboto Slab"; font-size: 19px; font-weight: bold; padding: 7px 0 0; }
.news__date p { text-transform: uppercase; font-size: 13px; font-weight: normal; }
.news__comments { width: 60px; height: 60px; background-color: #222; color: #fff; text-align: center; font-family: "Roboto Slab"; font-size: 15px; padding: 7px 0 0; }
.news__tags { padding: 2px 0 11px 0; }
.news__tags ul { display: flex; flex-direction: row; }
.news__tags ul li { margin-right: 10px; }
.news__link { color: #A0A07E; }
.news__link:hover { color: #000; }
.news__descr a { color: #393939; font-size: 19px; line-height: 1.4; }
@media only screen and (max-width: 1024px) { .news__descr a { font-size: 14px; } }
@media only screen and (max-width: 650px) { .news__descr a { font-size: 19px; } }
.news__descr a:hover { color: #ed5126; }
.news__more { color: #ed5126; font-size: 12px; font-weight: bold; text-transform: uppercase; transition: all 0.2s ease-in; cursor: pointer; }
.news__more:after { content: "→"; }
.news__more:hover { color: #222; padding-left: 5px; }

@media only screen and (max-width: 768px) { .featured-product { display: none; } }
.featured-product__in { display: flex; flex-direction: row; position: relative; }
.featured-product__left { width: 25%; }
.featured-product__right { width: 75%; display: flex; flex-wrap: wrap; }
.featured-product__item { width: 33.33%; position: relative; }
.featured-product__item_big { width: 100%; }
.featured-product__item:hover .featured-product__descr { display: block; }
.featured-product__img img { display: block; width: 100%; height: 100%; }
.featured-product__descr { position: absolute; top: 0; left: 0; right: 0; bottom: 0; transition: all 0.2s ease-in; background: rgba(238, 78, 34, 0.93); display: none; }
.featured-product__info { position: absolute; padding: 0; top: 50%; margin-top: -65px; text-align: center; left: 20px; right: 20px; color: #ff0000; }
.featured-product__brand { font-family: Playball; font-size: 22px; padding-top: 15px; color: #fff; }
.featured-product__model { font-family: "Roboto Slab"; font-size: 24px; text-transform: uppercase; color: #fff; }
.featured-product__model:hover { color: #fff; }
@media only screen and (max-width: 1110px) { .featured-product__model { font-size: 15px; } }
.featured-product__price { font-family: "Roboto Slab"; font-size: 15px; padding-top: 6px; color: #fff; }

.limited-time { background: url("../img/bg-timer.jpg") top center no-repeat; text-align: center; }
.limited-time__btn { padding: 840px 0 50px; text-align: center; }

.countdown { text-align: center; }
.countdown-section { padding: 10px 30px; border-right: 1px solid #ccc; }
@media only screen and (max-width: 768px) { .countdown-section { padding: 5px 15px; } }
.countdown-section:last-child { border: none; }
.countdown-amount { font-family: Roboto; font-size: 30px; font-weight: 400; margin-right: 2px; }
@media only screen and (max-width: 768px) { .countdown-amount { font-size: 20px; } }
.countdown-period { font-family: Roboto; color: #ee4415; font-size: 14px; text-transform: uppercase; }
@media only screen and (max-width: 768px) { .countdown-period { font-size: 10px; } }

.drop-down { display: flex; flex-direction: row; padding: 50px 30px; background-color: #fff; }
.drop-down_megamenu { width: 1000px; background: #fff url("../img/dropdown-banner_1.jpg") no-repeat top right; }
.drop-down_clothing { width: 600px; background: #fff url("../img/dropdown-banner_2.jpg") no-repeat top right; }
.drop-down__item { margin: 0 30px; }
.drop-down__list { display: flex; flex-direction: column; padding: 10px 0; }
.drop-down__el { padding: 5px 0; }
.drop-down__title a { display: inline-block; font-size: 15px; font-weight: 800; text-transform: uppercase; border-bottom: 2px solid #ef4416; color: #1f1f1f; padding-bottom: 10px; }
.drop-down__title a:hover { color: #ef4416; }
.drop-down__link { font-size: 14px; font-weight: 500; line-height: 1.6; color: #949494; }
.drop-down__link:hover { color: #000; }
.drop-down__link:hover { color: #1f1f1f; }

.roundabout__btns { position: relative; }
.roundabout__btn { position: absolute; top: -175px; padding: 5px 10px; border: 1px solid #f0f0f0; cursor: pointer; }
.roundabout__btn:hover { background-color: rgba(204, 204, 204, 0.3); }
.roundabout__btn_prev { left: 0; }
.roundabout__btn_next { right: 0; }


